.navbar-toggler:focus,
.navbar-toggler:active {
  outline: none;
  box-shadow: none;
}

.navbar button.navbar-toggler {
  border: none;
  padding-right: 0;
}

.navbar-brand {
  user-select: none;
}

.overflow-ellipsis {
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
}
